// app/blogs/[id]/BlogPageClient.tsx (Client Component)
'use client';

import { useEffect, useState } from 'react';
import { FiFacebook, FiTwitter, FiLinkedin, FiCopy } from 'react-icons/fi';
import { Button } from '@/components/ui/button';
import { toast } from 'sonner';
import Link from 'next/link';

interface BlogPageClientProps {
    shareUrl: string;
    blog: {
        title: string;
        description: string; // Add other properties as needed
    };
}

export default function BlogPageClient({ shareUrl, blog }: BlogPageClientProps) {
   const [currentUrl, setCurrentUrl] = useState('');

    useEffect(() => {
        if (typeof window !== 'undefined') {
            setCurrentUrl(window.location.href);
        }
    }, []);


   const handleCopy = () => {
    if (currentUrl) {
      navigator.clipboard.writeText(currentUrl)
        .then(() => {
          // Show a success message (using a simple alert for this example)
          toast.success("URL Copied to clipboard");

         //  alert('Blog URL copied to clipboard!'); //Consider using toast for better ux and style
        })
        .catch((err) => {
          // Handle errors (e.g., the user denied clipboard access)
          console.error('Failed to copy URL: ', err);
          toast.error("Failed to copy URL! Please try again.");
        });
    }
  };


    return (
        <>
           <div className="flex gap-4 mt-8">
              
                <Button variant="outline" size="icon" asChild>
                <a
                href={`https://www.facebook.com/sharer/sharer.php?u=${shareUrl}`}
                target="_blank"
                rel="noopener noreferrer"
                >
                <FiFacebook className="h-5 w-5" />
                </a>
          </Button>
          <Button variant="outline" size="icon" asChild>
            <a
              href={`https://twitter.com/intent/tweet?url=${shareUrl}&text=${encodeURIComponent(
                blog.title
              )}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <FiTwitter className="h-5 w-5" />
            </a>
          </Button>
          <Button variant="outline" size="icon" asChild>
            <a
              href={`https://www.linkedin.com/shareArticle?mini=true&url=${shareUrl}&title=${encodeURIComponent(
                blog.title
              )}&summary=${encodeURIComponent(blog.description)}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <FiLinkedin className="h-5 w-5" />
            </a>
          </Button>
            <Button variant="outline" size="icon" onClick={handleCopy}>
               <FiCopy className='h-5 w-5'/>
            </Button>

           </div>
        </>
    );
}
